header{
	margin-bottom: rem(30px);
}
.header__top{
	font-size: rem(13px);
	background-color: $dark;
}
.header__top-mnu{
	li{
		display: inline-block;
		margin-right: rem(35px);
		a{
			font-size: rem(13px);;
			color: #fff;
			text-decoration: none;
			&:hover, &:active, &:focus{
				color: #fff;
			}
			
		}
	}

}
.row{
	@include row-flex;
	@include wrapper;
	
}
.header__top-col{
	display: -webkit-flex;
	display: -moz-flex;
	display: -ms-flex;
	display: -o-flex;
	display: flex;

	justify-content: space-between;
	align-items: center;
	@include col;
	@include col-12;
	

}
.header__top-right{
	font-size: 0;
}
.header__top-var, .icon-cart, .header__top-login, .header__top-price, .header__top-cart-items, .header__top-cart-items-count{
	display: inline-block;
	vertical-align: middle;
	color: #fff;
	font-size: rem(13px);
}
.icon-cart{
	margin-right: 5px;
	font-size: 1.2rem;
	color: #fff;
}
.header__top-cart-items-count, .header__top-cart-items{
	color: #dcc5b8;
	margin-left: 3px;
	
}
.header__top-login{
	padding: rem(17px) rem(21px);
	border-left: 1px solid #5a3a3a;
	text-decoration: none;
	&:hover, &:active, &:focus{
		background-color: $accent;
		border-left-color: transparent;
	}
}
.header__top-cart-items{
	margin-right: rem(20px);
	
}

.header__middle{
	background-color: $light;
}
.header__middle-col{
	@include col;
	@include col-12;
	display: -webkit-flex;
	display: -moz-flex;
	display: -ms-flex;
	display: -o-flex;
	display: flex;

	justify-content: space-between;
	align-items: center;
}
.header__middle-logo{
	max-width: 187px;
	flex-basis: 187px;
	source, img{
		display: block;
		max-width: 100%;
	}
}
.header__middle-right{
	flex-basis: auto;
}
.header__main-mnu{
	left: -100%;
	font-size: 0;
	li{
		display: inline-block;
		margin-left: rem(35px);
		a{	
			position: relative;
			display: block;
			padding: rem(27px) 0;
			color: #fff;
			text-decoration: none;
			font-size: 1rem;

			&:hover, &:active, &:focus{
				text-decoration: none;
				color: $accent;
				&:after{
					display: block;
				}
			}

			&:after{
				content: '';
				display: none;
				width: 100%;
				height: 5px;
				position: absolute;
				bottom: 0;
				left: 0;
				background-color: $accent;
			}
				
		}
	}
}
.header__main-mnu--open{
	left: 0 !important;
}

.header__bottom{
	background-color: #f0efee;
}
.header__bottom-col{
	min-height: 597px;
	display: -webkit-flex;
	display: -moz-flex;
	display: -ms-flex;
	display: -o-flex;
	display: flex;

	align-items: center;
	@include col;
	@include col-6;
	@include col-md-6;
	@include col-xs-12;
}
.header__bottom-visual{
	position: relative;
	width: 100%;
	height: 100%;
	img{
		position: absolute;
		right: 0;
		top: -88px;
	}
}

/*-----------------------propositions----------------------*/
.propositions{
	margin-bottom: rem(85px);
}
.propositions-col{
	@include col;
	@include col-6;
	@include col-sm-12;
}
.icon-crown{
	position: absolute;
	left: rem(47px);
	top: rem(47px);
	font-size: 3rem;
	color: #fff;
}
.propositions__block{
	height: 540px;
	padding: 45px;
	position: relative;
	background: 0 0 no-repeat;
	background-size: cover;
	text-align: center;
	box-sizing: border-box;

	h2{
		color: #fff;
	}
	&--blue{
		h2{
			color: #42D9CE;
			
		}
	}
	&--white{
		background-color: $accent;
		h2{
			color: #fff;
			
		}
	}
	&--bottom{
		img{
			position: absolute;
			bottom: 0;
			left: 0;
			right: 0;
			top: auto !important;
			margin: 0 auto;
		}
	}
	
}
.propositions-col__top{
	.propositions__block{
		height: initial;
		width: 100%;
		position: relative;

		img{
			position: absolute;
			left: 0;
			bottom: 0
		}
	}
}
.propositions-col--flex{
	display: -webkit-flex;
	display: -moz-flex;
	display: -ms-flex;
	display: -o-flex;
	display: flex;

	flex-wrap: wrap;
	// align-content: stretch;
	// flex-direction: column;
	// // justify-content: space-between;

	.propositions-col__top{
		width: 100%;
		display: -webkit-flex;
		display: -moz-flex;
		display: -ms-flex;
		display: -o-flex;
		display: flex;

		margin-bottom: rem(30px);
	}
}
.propositions-col__bottom{
	width: 100%;
	display: -webkit-flex;
	display: -moz-flex;
	display: -ms-flex;
	display: -o-flex;
	display: flex;

	justify-content: space-between;
	// flex-wrap: wrap;
	// align-items: flex-end;

	.propositions__block{
		height: initial;

		 width: calc(50% - 15px);
	

		img{
			position: absolute;
			left: 0;
			top: rem(-20px);
		}
	}
}
.propositions__slogan{
	color: #fff;

}


/*-----------------------end of propositions----------------------*/

/*-----------------------cards----------------------*/
.cards{
	margin-bottom: rem(79px);
	.row{
		@include justify-content-xs(center);

	}
}
.cards-col{
	@include col;
	@include col-3;
	@include col-md-4;
	@include col-sm-6;
	@include col-xs-10;
	
	margin-bottom: rem(30px);
}
.tabs__list{
	width: 100%;
	margin-bottom: rem(45px);
	text-align: center;

	.tabs__item--active{
		.tabs__link{
			color: $dark;
			font-weight: bold;
		}
	}
}
.tabs__link{
	margin: 0 15px;
	font-size: rem(30px);
	color: $gray;
	text-transform: uppercase;
}
.tabs__item{
	display: inline-block;
}
.tabs__content{
	.tabs__item{
		display: none;
		width: 100%;
	}
	#item1{
		display: block;
	}
}
.card{
	position: relative;
	display: -webkit-flex;
	display: -moz-flex;
	display: -ms-flex;
	display: -o-flex;
	display: flex;
	flex-wrap: wrap;
	padding-top: rem(22px);
	padding-bottom: rem(22px);
	padding-left: rem(30px);
	border: 1px solid $gray;
	
	&--discount{
		.card__price{
			color: $accent;
		}
		.card__sale{
			display: block;
		}
		.card__pers{
			display: block;
		}
	}
}
.card__top{
	height: 54px;
}
.card__bottom{
	margin-top: auto;
}
.card__img{
	width: 100%;
	text-align: center;
	img{
		width: 80%;
		display: inline-block;
	}
}
.card__price{
	font-size: rem(24px);
	color: $gray;

}
.card__pers{
	display: none;
	padding: 7px;
	position: absolute;
	top: rem(22px);
	right: 0;
	color: #fff;
	background-color: $accent;
}
.card__sale{
	display: none;
	color: $gray;
	text-decoration: line-through;
}
.card__bottom{
	color: $gray;
	line-height: rem(26px);
}
.card__gender{
	font-weight: bold;
}
.card__articul{
	font-size: 13px;
}
/*-----------------------end of cards----------------------*/

/*-----------------------logotypes----------------------*/
.logotypes-col{
	@include col;
	@include col-2;
	@include col-md-3;
	@include col-sm-4;
	@include col-xs-12;
	margin-bottom: rem(30px);
	text-align: center;
}
.adidas{
	display: inline-block;
	@include sprite($adidas);
}
.nike{
	display: inline-block;
	@include sprite($nike);
}
.puma{
	display: inline-block;
	@include sprite($puma);
}
.dasics{
	display: inline-block;
	@include sprite($dasics);
}
.nb{
	display: inline-block;
	@include sprite($nb);
}
.fila{
	display: inline-block;
	@include sprite($fila);
}
/*-----------------------end of logotypes----------------------*/

/*-----------------------testimonials----------------------*/
.testimonials{
	padding: rem(98px) 0;
	position: relative;
	background: $dark url(../img/bg_testim.jpg) 0 0 no-repeat;
	background-size: cover;

	
	&:before{
		content: '';
		position: absolute;
		top: 0;
		left: 0;	
		width: 100%;
		height: 100%;
		background-color: rgba(#482423, .9);
		z-index: 0;
	}
	.row{
		@include  justify-content(center);
		position: relative;
		z-index: 2;
	}
}
.testimonials-col{
	@include col;
	@include col-2;
	@include col-sm-4;
	@include col-xs-12;
	text-align: center;

	&.col-12{
		@include col-12;
	}
}
.testimonials-content-col{
	@include col;	
	@include col-12;	
}
.userpic{
	width: 64px;
	height: 64px;
	margin: 0 auto;	
	margin-bottom: rem(15px);
	display: -webkit-flex;
	display: -moz-flex;
	display: -ms-flex;
	display: -o-flex;
	display: flex;

	justify-content: center;
	align-items: center;

	border-radius: 35px;
	border: 2px solid #fff;
	overflow: hidden;
}
.username{
	font-size: 1rem;
	color: #fff;
	font-weight: bold;
}
.icon-star, .icon-star-empty{
	color: $accent;
	font-size: 1rem;
}
.testimonials__inner{
	display: block;
	opacity: .5;
	position: relative;
	text-decoration: none;

	&:hover, &:active, &:focus{
		text-decoration: none;
	}
	

	// &-triangle{
	// 	content: '';
	// 	// display: none;
	// 	visibility: hidden;
	// 	width: 0;
	// height: 0;
	// border-left: 25px solid transparent;
	// border-right: 25px solid transparent;
	// border-bottom: 25px solid rgba(#fff, .1);
	// 	position: absolute;
	// 	bottom: rem(-40px);
	// 	left: 0;
	// 	right: 0;
	// 	margin: 0 auto;
	// 	// background-color: rgba(#fff, .02);
		
	// }
	&--active{
		.testimonials__inner-triangle{
			visibility: visible;
		}
		opacity: 1;
	}
}
.testimonials__inner-triangle{
	content: '';
		// display: none;
		visibility: hidden;
		width: 0;
	height: 0;
	border-left: 25px solid transparent;
	border-right: 25px solid transparent;
	border-bottom: 25px solid rgba(#fff, .1);
		position: absolute;
		bottom: rem(-40px);
		left: 0;
		right: 0;
		margin: 0 auto;
}
.testimonials__content{
	display: none;
	
	padding: rem(41px);
	margin-top: rem(40px);
	background-color: rgba(#fff, .1);

	&--active{
		display: block;
	}

}
.testimonials__inner-text{
	position: relative;
	width: 100%;
	display: none;
	box-sizing: border-box;
	padding: rem(10px);
	margin-bottom: 30px;
	margin-top: rem(40px);
	background-color: rgba(#fff, .1);

	&--active{
		display: block;
	}
	.testimonials__inner-triangle{
		visibility: visible;
		top: -25px;
	}
}
.testimonials-content-col{
	position: relative;
	.testimonials__inner-triangle{
		visibility: visible;
		top: 15px;
		transition: left .5s ease;
	  margin: 0;
	  // margin-left: -25px;
	  left: calc(50% - 25px);
	}
}
/*-----------------------end of testimonials----------------------*/

/*-----------------------news----------------------*/
.news{
	h2{
		width: 100%;
		margin-bottom: rem(30px);
		text-align: center;
		text-transform: uppercase;
	}
}
.news-col{
	@include col;
	@include col-4;
	@include col-md-6;
	@include col-sm-12;

	display: -webkit-flex;
	display: -moz-flex;
	display: -ms-flex;
	display: -o-flex;
	display: flex;
	@extend .cover;

	min-height: 350px;
	padding: rem(18px) rem(30px);
	margin-bottom: rem(30px);
	color: #fff;
	font-size: rem(16px);
	line-height: rem(26px);
	background: $dark center no-repeat;
	background-size: cover;

	span{
		font-size: rem(13px);
		color: $accent;
		vertical-align: middle;
	}
}
.news-inner{
	margin-top: auto;
	position: relative;
	z-index: 1;
}
.icon-quote{
	color: $accent;
	font-size: 1rem;
	vertical-align: middle;
}
.news__date{
	font-weight: bold;
	text-transform: uppercase;
}
/*-----------------------end of news----------------------*/

/*-----------------------help----------------------*/
.help{
	background-color: $accent;
}
.help-col{
	@include col;
	@include col-6;
	@include col-sm-12;

	min-height: rem(200px);
	display: -webkit-flex;
	display: -moz-flex;
	display: -ms-flex;
	display: -o-flex;
	display: flex;

	justify-content: flex-end;
	align-items: center;
	color: #fff;

	&.fl-start{
		justify-content: flex-start;
	}
}
.help__block{
	.h3{
		font-size: rem(30px);
		text-transform: uppercase;
	}
}
.help__text{
	font-size: rem(16px);
}
.icon-mail{
	margin-right: rem(30px);
	color: #fff;
	font-size: 2rem;
}

/*-----------------------end of help----------------------*/

/*-----------------------features----------------------*/
.features{
	background-color: $light;
	.row{
		@include justify-content-sm(center);
		
	}
	.icon{
		margin-bottom: rem(20px);
		font-size: 2.5rem;
		color: $accent;
	}
}
.features-col{
	@include col;
	@include col-2;
	@include col-sm-3;
	@include col-xs-6;
	padding: rem(50px) 0;
	text-align: center;
}
.features-text{
	font-size: rem(16px);
	line-height: rem(26px);
	color: #fff;
}

/*-----------------------end of features----------------------*/

/*-----------------------contacts----------------------*/
.contacts{
	
	background-color: $light;
	color: #fff;

	.row{
		padding-top: rem(90px);
		@include justify-content-sm(center);
		border-top: 1px solid #7A5150;
	}

	.icon{
		font-size: 1.2rem;
	}

	&-img{
		display: -webkit-flex;
		display: -moz-flex;
		display: -ms-flex;
		display: -o-flex;
		display: flex;

		width: 53px;
		height: 53px;
		margin-right: rem(15px);
		background-color: $accent;

		justify-content: center;
		align-items: center;

		.icon-mail{
			margin: 0;
			font-size: 1rem;
		}
	}
	&-block{
		// width: 100%;
		display: -webkit-flex;
		display: -moz-flex;
		display: -ms-flex;
		display: -o-flex;
		display: flex;

		align-items: center;

		margin-bottom: rem(20px);
	}
	&-text{
		line-height: rem(26px);
	}
	&-title{
		text-transform: uppercase;
		color: $accent;
		font-weight: bold;
	}
	&-mnu-title{
		text-transform: uppercase;
		margin-top: 0;
		color: $accent;
	}
	&-menu{
		li{
			margin-bottom: rem(15px);
		}
		a{
			text-transform: capitalize;
			color: #fff;
			text-decoration: none;
			font-size: rem(15px);
			&:hover, &:active, &:focus{
				text-decoration: underline;
			}
			
		}
	}
}
.contacts-col-4{
	@include col;
	@include col-4;
	@include col-md-12;
	// @include col-sm-5;
	
	// @include col-xs-12;
	
	margin-bottom: rem(30px);
}
.contacts-col-2{
	@include col;
	@include col-2;
	@include col-md-6;
	// @include col-sm-5;
	// @include col-xs-6;
	margin-bottom: rem(30px);
}
.contacts__map{
	display: none;
	@include col;
	@include col-12;
}
/*-----------------------end of contacts----------------------*/
/*-----------------------footer----------------------*/
footer{
	color: #fff;
	background-color: $light;

	.row{
		@include justify-content-sm(center);
		padding-top: rem(40px);
		padding-bottom: rem(40px);
		border-top: 1px solid #fff;
	}
}
.footer-col{
	@include col;
	@include col-4;
	@include col-sm-12;
}
.footer__logo{
	@extend .header__middle-logo;
}
.socials{
	display: inline-block;
	li{
		display: inline-block;
		margin: 0 10px;
		vertical-align: middle;

		.icon{
			color: #fff;
			font-size: 1.5rem;

			&:hover{
				color: $accent;
			}
		}
	}
}

/*-----------------------end of footer----------------------*/
