html{
	
	font-size: 16px;
}
body{
	font-family: Arial, "Helvetica Neue", Helvetica, sans-serif;
	font-weight: normal;
	min-width: 320px;
	position: relative;
	// line-height: 1.6;
	-webkit-font-smoothing: antialiased;
	overflow-x: hidden;
	// background-color: #482423;
}
body input:focus:required:invalid,
body textarea:focus:required:invalid{
	color: red;
}
body input:required:valid,
body textarea:required:valid{
	color: green;
}
input{
	outline: none;
}

ul, li{
	margin: 0;
	padding: 0;
	list-style-type: none;
}
.toggle-mnu{

  display: none;
  width: 28px;
  height: 28px;
  margin-top: 14px;

  span:after, span:before{

    content: "";
    position: absolute;
    left: 0;
    top: 9px;
  } 

  span:after{

    top: 18px;
  }

  span{

    position: relative;
    display: block;
  } 

  span, span:after, span:before{

    width: 100%;
    height: 2px;
    background-color: #fff;
    transition: all 0.3s;
    backface-visibility: hidden;
    border-radius: 2px;
  }


  &.on span{

    background-color: transparent;
  } 

  &.on span:before{

    transform: rotate(45deg) translate(-1px, 0px);
  } 

  &.on span:after{

    transform: rotate(-45deg) translate(6px, -7px);
  } 
}
.responsive-img{
	width: 100%;
	display: block;
}
.clearfix{
	@include clearfix;
}
.cover{
  position: relative;
  &:before{
    content: '';
    width: 100%;
    height: 100%;
    position: absolute;
    left: 0;
    top: 0;
    z-index: 0;
    background-color: rgba($dark, 0.7);
  }
}
.button{
  display: inline-block;
  border: none;
  color: #fff;
  text-decoration: none;
  background-color: $dark;
  padding: 15px 45px;
  font-size: 13px;
  text-transform: uppercase;
  font-weight: 600;
  letter-spacing: 3px;
  border-radius: 2px;
  text-align: center;
  position: relative;
  outline: none;
  transition: background-color .1s ease;
  
  &:hover{
     background-color: lighten($dark, 5%);
  }
   
    
  &:active{
   background-color: darken($dark, 5%);
  }
   
}
.text{
  &-center{
    text-align: center;
  }
  &-right{
    text-align: right;
  }
}
.justify{
	content: '';
	width: 100%;
	height: 0;
	display: inline-block;
	overflow: hidden;
	visibility: hidden;
	line-height: 0;
}
.hidden{
	display: none;
}
