/*==========  Desktop First Method  ==========*/

 

/* Large Devices, Wide Screens */
@media only screen and (max-width : 1200px){

}
	
/* Medium Devices, Desktops */
@media only screen and (max-width : 992px){
	.toggle-mnu{
		display: block;
	}
	.header__middle{
		position: relative;
	}
	.header__main-mnu{
		position: absolute;
		width: 50%;
		top: 100%;
		border-top: 1px solid #fff;
		
		z-index: 1000;
		background-color: $dark;
		transition: left .5s ease;
		li{
			margin: 0;
			padding-left: 10%;
			display: block;
			background-color: $light;
			&:hover{
				background-color: $dark;
			}

			a{
				padding: 10px 0;


				&:hover:after{
					display: none;
				}
			}
		}
	}
}
	
@media only screen and (max-width : 960px){
	.contacts-col-4{
		display: -webkit-flex;
		display: -moz-flex;
		display: -ms-flex;
		display: -o-flex;
		display: flex;

		justify-content: space-between;
	}
}

@media only screen and (max-width : 780px){
	.contacts-col-4{
		display: -webkit-flex;
		display: -moz-flex;
		display: -ms-flex;
		display: -o-flex;
		display: flex;
		// justify-content: center;
		flex-direction: column;

	}
	.contacts-block{
		width: auto;
	}
}	
/* Small Devices, Tablets */
@media only screen and (max-width : 768px){

	.header__top-col{
		flex-direction: column;
		padding: 15px 0;
	}
	.header__top-mnu{
		li{
			margin-right: rem(15px);;

		}
	}
	.help{
		padding: 30px 0;
	}
	.help-col{
		min-height: initial;
		justify-content: center;
		align-items: center;

		&.fl-start{
			margin-bottom: 60px;
		}
	}
	.help__block{
		// margin-bottom: 60px;
	}
	.footer-col{
		margin-bottom: rem(30px);
		text-align: center;
	}
	.footer__logo{
		display: inline-block;
	}
