@media only screen and (max-width : 560px){
	.header__bottom-col{
	min-height: initial;
	}
	.header__bottom-visual{
		img{
			position: static;
			width: 100%;
			margin-left: -30px;
		}
	}
	.testimonials__bottom{
		display: none;
	}
}
	

/* Extra Small Devices, Phones */
@media only screen and (max-width : 480px){
	html{
		font-size: 13px;
	}
	.propositions-col__bottom{
		flex-wrap: wrap;
		.propositions__block{
			width: 100%;

			img{
				width: 50%;
				left: 0;
				margin: 0;
			}
		}
	}
}
		

